$cell-width: 100px;
$cell-height: 100px;
$cells-gap: 1px;
$max-cells-per-row: 7;
$padding-for-wrapper : 10px;
$max-width:
  ($cell-width * $max-cells-per-row)
  + $max-cells-per-row + ($padding-for-wrapper * 2);

$days-in-week: 7;
$days-in-week:
  "mon" 0,
  "tue" 1,
  "wed" 2,
  "thu" 3,
  "fri" 4,
  "sat" 5,
  "sun" 6;

//colors
$cell-bg-color: #eee;
$cell-bg-color-on-hover: pink;
